<template>
  <div class="login">
    <h1>米蕉账号注册</h1>
    <van-image width="100" height="100" :src="require('@/assets/橘子.png')" />

    <div class="title">让每个人都能享受科技的乐趣</div>
    <div class="username flex">
      <div class="tag">账号</div>
      <div>
        <input type="text" v-model="username" placeholder="请输入账号" />
      </div>
    </div>
    <div class="password flex">
      <div class="tag">密码</div>
      <div>
        <input type="password" v-model="password" placeholder="请输入密码" />
      </div>
    </div>
    <div class="password flex">
      <div class="tag">确认密码</div>
      <div>
        <input type="password" v-model="psw" placeholder="请输入密码" />
      </div>
    </div>

    <div class="btn" @click="register">注册</div>
  </div>
</template>
  
  <script>
import { Toast } from "vant";
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      username: "",
      password: "",
      psw: "",
      users: JSON.parse(localStorage.getItem("users")) || [],
    };
  },
  methods: {
    ...mapMutations(["userLogin"]),
    register() {
      if (this.psw.trim() !== this.password.trim()) {
        Toast("密码不一致");
      } else {
        let registered = this.users.find((item) => {
          return item.username === this.username.trim();
        });
        if (registered) {
          Toast("账号已注册，请登录");
        } else {
          this.users = [
            ...this.users,
            { username: this.username.trim(), password: this.password.trim() },
          ];
          console.log(this.users);
          localStorage.setItem("users", JSON.stringify(this.users));
          Toast("注册成功");
          setTimeout(() => {
            this.$router.replace({
              path: "/login",
              query: { target: this.$route.query.target },
            });
            this.username = "";
            this.password = "";
            this.psw = "";
          }, 1000);
        }
      }
    },
  },
};
</script>
  
  <style lang="less" scoped>
.login {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  text-align: center;
  background: #000000;
  color: #fff;
  h1 {
    color: var(--text-secondary-color);
    font-size: 20px;
    font-weight: 500;
    line-height: 27px;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0;
    padding: 0.8rem 0;
  }
  .title {
    color: #737373;
    margin: 14px auto 0;
    padding: 0 28px;
  }
  .flex {
    display: flex;
    align-items: center;
    border: 2px solid transparent;
    color: hsla(0, 0%, 100%, 0.95);
    margin: auto;
    width: 70vw;
    height: 1rem;
    background: #262626;
    border-radius: 16px;
    margin-top: 0.7rem;
    padding: 0 14px;

    .tag {
      padding: 0 0.2rem;
    }

    input {
      background: #262626;
      border: 0px;
      height: 50%;
      &::placeholder {
        color: #757575;
      }
    }
  }
  .btn {
    width: 70vw;
    margin: 20px auto;
    background: rgba(39, 122, 247, 0.3);
    border-radius: 14px;
    padding: 12px;
    margin-top: 0.7rem;
  }
  .register {
    color: #737373;
    font-size: 0.25rem;
    display: flex;
    justify-content: space-around;
    margin-top: 0.2rem;
  }
}
</style>